import React from 'react';
import { NavLink } from 'react-router-dom';
const Common = (props) => {

    return (
        <>
            <section id='header' className="d-flex align-items-center" >
                <div className="container-fluid nav_bg">
                    <div className="row" >
                        <div className="col-10 mx-auto" >
                            <div className='row'>
                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center">
                                    <h1> {props.name} {props.id === "1" ? <strong>Business with us</strong> : null} </h1>
                                    <h2 className="my-3">Call now to place an Order</h2>
                                    <div className="mt-3">
                                        <NavLink to={props.visit} className="btn btn-info">{props.btnName}</NavLink>
                                    </div>
                                </div>
                                <div className="col-lg-6 mb-8 order-1 order-lg-2 header-img">
                                    <img src={props.imgsrc} className=" img-fluid animated" alt="Common_img" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Common;